* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: Arial, sans-serif;
    background-color: #E5D596;
    padding: 20px;
}
.add-entry-container {
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin: 20px 0 0 20px;
}
.error-message {
    color: red;
    font-weight: bold;
    margin: 10px;
    text-align: center;
  }
  

.form-container {
    display: flex;
    flex-direction: column;
    width: 1500px;
    height: 600px;
    padding: 5px;
}

h2 {
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: #998F65;
}

.form-group {
    margin-bottom: 15px;
}

label {
    font-size: 1.1rem;
    margin-bottom: 5px;
    display: block;
}

input[type="date"],
textarea {
    width: 10%;
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}

textarea {
    resize: vertical;
    width: 90%;
    height: 400px;
    max-height: 400px;
}

.save-btn {
    padding: 10px 20px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2rem;
    transition: background-color 0.3s ease;
}

.save-btn:hover {
    background-color: #776F4F;
}
