
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Arial', sans-serif;
    background-color: #E5D596;
    ;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.active-container{
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    height: 200px;
    width: 600px;
}
.lr-container {
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    padding: 2rem;
    width: 100%;
    max-width: 400px;
}

.lr-form h2 {
    text-align: center;
    margin-bottom: 1rem;
    color: #333333;
}

.input-group {
    margin-bottom: 1.5rem;
}

.input-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: #555555;
}

.input-group input {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #cccccc;
    border-radius: 5px;
    font-size: 1rem;
}

.input-group input:focus {
    border-color: #3b82f6;
    outline: none;
    box-shadow: 0 0 3px rgba(59, 130, 246, 0.5);
}

.lr-button {
    width: 100%;
    padding: 0.75rem;
    background-color: #998F65;
    border: none;
    border-radius: 5px;
    color: #ffffff;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.lr-button:hover {
    background-color: #776F4F;
}

.text {
    text-align: center;
    margin-top: 1rem;
    color: black;
}

.text a {
    color: #998F65;
    text-decoration: none;
}

.text a:hover {
    text-decoration: underline;
}
