* 
.logout-btn {
    position: absolute;
    top: 10px;
    left: 10px;
    padding: 10px 15px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.logout-btn:hover {
    background-color: #776F4F;
}


.main-container {
    display: flex;
    justify-content: space-between;
    width: 1450px;
    height: 600px;
    margin-top: 10px; 
}

.left-container {
    width: 30%;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 5px;
    margin-left: 5px;
    margin-bottom: 5px;
}

.left-container h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
}

.entries-list {
    list-style-type: none;
    padding: 0;
    margin-top: 20px;
    max-height: 450px;       
    overflow-y: auto;       
}


.entry-item {
    display: flex;          
    align-items: center;    
    margin-bottom: 10px;   
}

.entry-data {
    margin-right: 10px; 
}

.view-entry-btn {
    padding: 8px 12px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-left: auto;
}
#entry-details h3 {
    margin-bottom: 5px;
}

.view-entry-btn:hover {
    background-color: #776F4F;
}

.middle-container {
    width: 60%;
    background-color: #ffffff;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 5px;
}

.middle-container h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
}

#selected-entry-text {
    font-size: 1.1rem;
    margin-bottom: 20px;
}

.edit-entry-btn {
    padding: 10px 15px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
}

.edit-entry-btn:hover {
    background-color: #776F4F;
}

.right-container {
    width: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    border-radius: 10px;
}

.add-entry-btn {
    padding: 10px 15px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.add-entry-btn:hover {
    background-color: #776F4F;
}

.container {
    display: flex;
    justify-content: center; 
}

.edit-button{
    padding: 10px 15px;
    background-color: #998F65;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    width: 120px;
    margin-top: 20px;
    margin-left: 200px;
}

.edit-button:hover{
    background-color: #776F4F;
}

.entries-list li {
    margin-top: 5px;
    cursor: pointer;
    transition: color 0.3s, font-weight 0.3s;
  }
  
  .entries-list li:hover {
    color: #E5D596;
    font-weight: bold;
  }

  .content {
    margin-top: 10px;
    width: 800px;
    height: 400px;
    border: 1px solid #776F4F; 
    border-radius: 10px; 
    padding: 10px;
}

  
  